* {
  box-sizing: border-box;
}

@media (max-width: 600px) {
  :root {
    --lg: 20px !important;
  }
}
html {
  height: 100%;
}

body {
  font: 16px/1.4 "Jost", sans-serif;
  color: #2f2c26;
  margin: 0;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

audio,
canvas,
img,
video {
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
  vertical-align: top;
}

img {
  border-style: none;
  max-width: 100%;
  display: inline-block;
  outline: none !important;
}

a {
  color: #345dee;
}
@media (min-width: 992px) {
  a:hover {
    color: #6682d4;
  }
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

svg:not(:root) {
  overflow: hidden;
}

svg {
  fill: currentcolor;
  max-width: 100%;
}

.link-block {
  text-decoration: none;
  color: inherit;
}

.hidden {
  display: none;
}

.disable {
  opacity: 0.5;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.svg-icon {
  display: inline-block;
  width: 1em;
  height: 1em;
}

.contact {
  text-decoration: none;
  display: flex;
  align-items: center;
}
.contact__ico {
  font-size: 26px;
  margin-right: 0.5em;
}
.contact__pre {
  font-size: 14px;
}
.contact__txt {
  font-weight: 500;
  font-size: 18px;
}

@media (min-width: 767px) {
  .section-intro {
    display: grid;
    gap: 20px 40px;
    align-items: end;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 80px;
  }
  .section-intro p {
    font-size: 18px;
    margin: 0 0 0.5em;
  }
}
.section-intro--title-line {
  align-items: start;
}
.section-intro--title-line .title-section {
  grid-column: span 2;
  margin-bottom: 2rem;
}

.rating span {
  display: inline-block;
  width: 1em;
  height: 1em;
  background-repeat: no-repeat;
  background-size: cover;
}

.rating[data-rating="4"] span:nth-child(5) {
  opacity: 0.3;
}

.rating[data-rating="3"] span:nth-child(5),
.rating[data-rating="3"] span:nth-child(4) {
  opacity: 0.3;
}

.rating[data-rating="2"] span:nth-child(5),
.rating[data-rating="2"] span:nth-child(4),
.rating[data-rating="2"] span:nth-child(3) {
  opacity: 0.3;
}

.rating[data-rating="1"] span:nth-child(5),
.rating[data-rating="1"] span:nth-child(4),
.rating[data-rating="1"] span:nth-child(3),
.rating[data-rating="1"] span:nth-child(2) {
  opacity: 0.3;
}

input:not([type=submit]),
input:not([type=checkbox]),
input:not([type=radio]),
textarea {
  padding: 1em 2em;
  border-radius: 100px;
  border: none;
  display: block;
}

input:focus,
textarea:focus {
  outline: none;
  box-shadow: 0 0 5px rgba(102, 130, 212, 0.5);
}
input:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {
  opacity: 0.5;
}
input:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {
  opacity: 0.5;
}
input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
  opacity: 0.5;
}
input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  opacity: 0.5;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0.5;
}

.form {
  max-width: 650px;
  margin: 0 auto;
}

.form-line {
  position: relative;
  margin-bottom: 30px;
}
.form-line input:not([type=checkbox]):not([type=radio]):not([type=submit]) {
  width: 100%;
}
.form-line--inline {
  display: flex;
  gap: 40px;
}

.form__title {
  font-family: "Prata", sans-serif;
  font-size: 40px;
  line-height: 1.2;
  margin: 0 0 0.5em;
}

.form__txt {
  font-size: 18px;
  margin: 0 0 2.5em;
}

.form__footer {
  margin: 0;
}

.popup-form {
  display: none;
  max-width: 500px;
}

.btn {
  display: inline-block;
  flex: 0 0 auto;
  font-weight: 500;
  padding: 1em 1em 1em;
  text-decoration: none;
  line-height: 1;
  border-radius: 0.5em;
  border: none;
  appearance: none;
  color: #2f2c26;
  position: relative;
  overflow: hidden;
  background: #e1e5f1;
  border-radius: 50px;
  text-align: center;
}
.btn:not(.btn--title):hover {
  cursor: pointer;
  box-shadow: 0 0 0.8em rgba(47, 44, 38, 0.3);
}
.btn--title {
  font-weight: 400;
  color: #6682d4;
  padding: 0.5em 1.5em;
  margin-bottom: 50px;
}
.btn--big {
  padding: 1.5em 3em;
}
.btn--dark {
  background-color: #6682d4;
  color: #fff;
}
.btn--dark:hover {
  color: #fff;
}

.form-call-cnt {
  text-align: center;
  background: #eceef2 url("../img/bg/bg_form_call.png") no-repeat bottom center;
}

.nav {
  margin: auto;
  font-family: "Prata", sans-serif;
}
.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}
.nav__item {
  position: relative;
}
.nav__link {
  display: block;
  text-decoration: none;
  color: #2f2c26;
}

@media (min-width: 992px) {
  .nav > .nav__list > .nav__item > .nav__link {
    padding: 15px clamp(0.5em, 0.25vw, 2em);
  }

  .nav__item-sub > ul {
    position: absolute;
    min-width: 150px;
    left: 0;
    top: 100%;
    z-index: 100;
    background-color: #fff;
    filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.3));
    flex-direction: column;
    visibility: hidden;
    opacity: 0;
    transform: translateY(100px);
    transition: all 0.3s 0.3s;
  }
}
.burger {
  padding: 10px;
  display: inline-block;
  cursor: pointer;
}
.burger__el {
  position: relative;
  display: block;
  height: 2px;
  width: 20px;
  background-color: #000;
}
.burger__el:before, .burger__el:after {
  content: "";
  position: absolute;
  height: 2px;
  width: 20px;
  background-color: #000;
  transition: all 0.3s;
  transform-origin: 50% 50%;
}
.burger__el:before {
  transform: translateY(-4px);
}
.burger__el:after {
  transform: translateY(4px);
}
.burger.is-open .burger__el {
  background-color: transparent;
}
.burger.is-open .burger__el:before {
  transform: rotate(-45deg);
}
.burger.is-open .burger__el:after {
  transform: rotate(45deg);
}

.hc-nav-trigger {
  position: relative;
  top: 0;
  margin-left: 40px;
}

.hc-offcanvas-nav .nav__list {
  flex-direction: column;
}

.inner-nav {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 30px 0 0;
}
@media (min-width: 767px) {
  .inner-nav--right {
    margin-top: 0;
    grid-column-start: 2;
  }
}
.inner-nav__item:not(:last-child) {
  margin-right: 34px;
}
.inner-nav__link {
  text-decoration: none;
  color: #2f2c26;
  font-weight: 500;
  transition: color 0.2s;
}

.nav-catalog {
  margin: 80px 0 0;
  padding: 0;
  display: flex;
  list-style: none;
  border-bottom: 1px solid #eceef2;
}
.nav-catalog__item:not(:first-child) {
  margin-left: 50px;
}
.nav-catalog__item.active .nav-catalog__link {
  border-color: #6682d4;
}
.nav-catalog__link {
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  color: #2f2c26;
  padding-bottom: 1em;
  border-bottom: 4px solid transparent;
}

:root {
  --lg: 40px;
}

pre {
  font-family: inherit;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

* {
  min-width: 0;
}

a {
  transition: all 0.3s;
}

.page {
  flex: 1 0;
}

.container,
[class*=_container] {
  max-width: 1840px;
  margin: 0 auto;
  width: 100%;
  padding: 0 15px;
}
@media (min-width: 1280px) {
  .container,
[class*=_container] {
    padding: 0 60px;
  }
}

a[href*=tel] {
  text-decoration: none;
  color: inherit;
  white-space: nowrap;
}

.link-invert {
  color: #fff;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.3s;
}
.link-invert:hover {
  color: #fff;
  text-decoration: underline;
}

.iconsvg {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  vertical-align: middle;
}

.header_news_line {
  background: #6682d4 url("../img/bg/bg-news-line.jpg") no-repeat center/cover;
  color: #fff;
  padding: 1rem 0;
}
.header_news_line__container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header_news_line__txt {
  font-size: 13px;
}
@media (min-width: 767px) {
  .header_news_line__txt {
    font-size: 16px;
  }
}
.header_news_line__link {
  font-size: 12px;
  margin-left: 2.5rem;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  margin: 40px 0 80px 0;
}
.header__contacts {
  display: flex;
}
.header .contact:not(:first-child) {
  margin-left: 40px;
}

.logo {
  margin: 0;
  flex: 0 0 250px;
  color: #6682d4;
  line-height: 1.2;
  text-decoration: none;
  transition: all 0.3s;
}
.logo span {
  display: block;
}
.logo--header {
  font-size: 20px;
  margin-right: auto;
}

.section {
  margin-bottom: 120px;
}
.section--bg {
  padding: 100px;
  background-color: #eceef2;
}
.section--dark {
  background-color: #6682d4;
  color: #fff;
}

.title {
  font-family: "Prata", sans-serif;
  font-weight: normal;
}

.title-section {
  margin: 0;
  font-size: 40px;
  font-family: "Prata", sans-serif;
  line-height: 1.2;
  font-weight: 400;
  max-width: 650px;
}
.title-section--long {
  max-width: 900px;
}

.ico--round {
  position: relative;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  padding: 0.75em;
  color: #6682d4;
}
.ico--round .iconsvg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.light-txt {
  color: #9ba1b3;
}

.title-link {
  display: flex;
  align-items: flex-end;
  margin-bottom: 80px;
}

.more {
  color: #2f2c26;
  text-decoration: none;
  font-weight: 500;
}
.more--desktop {
  margin-left: auto;
  margin-bottom: 0.25em;
}

.tarif {
  background: #6682d4 url("../img/bg/bg__tarif.png") no-repeat left center;
}

.tarif-list {
  display: grid;
  gap: 40px;
}
@media (min-width: 992px) {
  .tarif-list {
    grid-template-columns: repeat(4, 1fr);
  }
}

.catalog-mini__cards {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.configurator {
  background: #eceef2 url("../img/bg/bg-configurator.png") no-repeat left top/cover;
}
.configurator__wrap {
  max-width: 600px;
}
.configurator__txt {
  margin: 1.75em 0 0;
  font-size: 18px;
}
.configurator__link {
  margin-top: 50px;
}
.configurator__link .btn {
  width: 320px;
}

.article-list {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(4, 1fr);
}

.religions {
  background: #eceef2 url("../img/bg/bg_right.png") no-repeat right top;
}

.religions-list {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(2, 1fr);
}

summary {
  display: block;
}

summary::-webkit-details-marker,
summary::marker {
  display: none;
}

.marker {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  position: relative;
  pointer-events: none;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
}

.marker::before,
.marker::after {
  content: "";
  display: block;
  width: 26px;
  height: 2px;
  background: #2f2c26;
  position: absolute;
  pointer-events: all;
}

.marker::after {
  transform: rotate(90deg);
}

.faq-list__item {
  padding: var(--lg);
  border: 1px solid #eceef2;
  margin-top: -1px;
  transition: all 0.3s;
}
.faq-list__item[open] {
  background-color: #eceef2;
}
.faq-list__q {
  position: relative;
  font-weight: 500;
  font-size: 22px;
  padding-right: 80px;
  cursor: pointer;
}
.faq-list__a {
  margin-top: 40px;
}
.faq-list__a p {
  margin-bottom: 0;
}
.faq-list__a p:first-child {
  margin-top: 0;
}
@media (min-width: 767px) {
  .faq-list__a {
    position: relative;
    padding-left: 50px;
  }
  .faq-list__a:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 2px;
    height: 36px;
    background-color: #6682d4;
    transform: skewX(-35deg);
    transform-origin: bottom right;
  }
}

.benefits {
  display: grid;
  gap: 40px;
  background: url("../img/bg/bg_benefits.png") no-repeat center center;
}
@media (min-width: 1024px) {
  .benefits {
    gap: 80px;
    grid-template-columns: 33% 1fr;
    background-position: 100% 0;
  }
}
@media (min-width: 1024px) {
  .benefits__cnt {
    padding: 80px 0;
  }
}
.benefits__img {
  background: url("../img/angel.jpg") no-repeat 50% 50%/cover;
}
.benefits-list {
  display: grid;
  gap: 40px;
}
@media (min-width: 767px) {
  .benefits-list {
    grid-template-columns: repeat(2, 1fr);
    margin-top: 80px;
    gap: 80px 100px;
  }
}

.benefit__ico {
  font-size: 46px;
  color: #6682d4;
}
.benefit__title {
  margin-top: 32px;
  font-weight: 500;
  font-size: 22px;
}
.benefit__txt {
  margin-top: 1em;
}

.service-cards {
  margin-top: 80px;
  display: grid;
  gap: 40px;
}
@media (min-width: 1400px) {
  .service-cards {
    grid-template-columns: repeat(4, 1fr);
  }
  .service-cards--index .service-card:nth-child(3), .service-cards--index .service-card:nth-child(4) {
    grid-column: span 2;
  }
}

.service-card {
  display: flex;
  flex-direction: column;
  text-decoration: none;
}
.service-card--service {
  background-color: #fff;
  padding: 40px;
  color: inherit;
}
.service-card--service:hover {
  color: #2f2c26;
}
.service-card--service:hover .service-card__ico {
  background-color: #6682d4;
  color: #fff;
}
.service-card--service .service-card__ico {
  color: #6682d4;
  background-color: #e1e5f1;
}
.service-card--tarif {
  color: #fff;
}
.service-card--tarif:hover {
  color: #fff;
}
.service-card--tarif:hover .service-card__ico {
  color: #fff;
}
.service-card--tarif:hover .service-card__arrow {
  border-color: #fff;
}
.service-card--tarif .service-card__ico {
  color: #fff;
  background-color: #5d78ca;
}
.service-card--tarif .service-card__arrow {
  font-size: 28px;
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 1px solid transparent;
  margin-left: -20px;
  transition: all 0.3s;
}
.service-card--tarif .service-card__arrow .iconsvg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.service-card__ico {
  font-size: 40px;
  padding: 1em;
  transition: all 0.3s;
}
.service-card__cnt {
  margin-top: auto;
  max-width: 500px;
}
.service-card__title {
  margin: 32px 0 0;
  font-family: "Prata", sans-serif;
  font-size: 22px;
  font-weight: 400;
}
.service-card__txt {
  margin: 1em 0 0;
}
.service-card__footer {
  margin-top: 1em;
}

.cart-img {
  color: #fff;
  text-decoration: none;
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 40px;
  z-index: 1;
  min-height: 500px;
}
@media (max-width: 1840px) {
  .cart-img {
    min-height: calc(
				250px + 250 *
					((100vw - 320px) / 1520)
			);
  }
}
.cart-img:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: -1;
  transition: background 0.3s;
}
.cart-img:hover {
  color: #fff;
}
.cart-img:hover:before {
  background-color: rgba(102, 130, 212, 0.5);
}
.cart-img:hover .cart-img__link {
  background-color: #6682d4;
}
.cart-img--religions .cart-img__title {
  font-size: 32px;
}
.cart-img__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -2;
}
.cart-img__link {
  border-radius: 50%;
  width: 80px;
  height: 80px;
  background-color: #eceef2;
  position: relative;
  font-size: 28px;
  color: #2f2c26;
  transition: all 0.3s;
  margin-bottom: 40px;
}
.cart-img__link .arrow-left {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.cart-img__data {
  padding-right: 0.5em;
  border-right: 1px solid;
}
.cart-img__rubric {
  margin-left: 0.3em;
}
.cart-img__cnt {
  margin-top: auto;
}
.cart-img__info {
  margin-bottom: 1em;
  font-size: 14px;
  text-transform: uppercase;
  color: #9ba1b3;
}
.cart-img__desc {
  margin: 1em 0 0;
}
.cart-img__title {
  font: 400 22px "Prata", sans-serif;
}

.card {
  display: block;
  margin: -1px 0 0 -1px;
  border: 1px solid #e1e5f1;
  text-decoration: none;
  color: #2f2c26;
  padding: 40px;
  transition: all 0.3s;
  position: relative;
}
.card:hover {
  color: #2f2c26;
  border-color: #6682d4;
  z-index: 1;
}
.card__img {
  position: relative;
  height: 240px;
  margin: -40px -40px 20px -40px;
}
.card__img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.card__rating {
  font-size: 18px;
  margin-bottom: 32px;
}
.card__rating span {
  background-image: url("../img/star-blue.svg");
}
.card__title {
  font: 400 22px "Prata", sans-serif;
  margin: 0 0 16px;
}
.card__desc {
  margin: 0 0 16px;
}
.card__price {
  margin-top: 32px;
  font: 400 22px "Prata", sans-serif;
}

.intro {
  margin-bottom: 120px;
}
.intro__container {
  background: url("../img/bg/bg-intro.png") no-repeat right center;
  background-size: auto 100%;
  background-origin: content-box;
}
.intro__title {
  font-size: 24px;
  font-size: clamp(24px, 5vw, 55px);
  margin: 0 0 0.75em 0;
}
.intro__footer {
  display: flex;
  align-items: center;
}
.intro__footer-btn {
  margin-right: 40px;
}

.swiper-slide {
  position: relative;
}
.swiper-slide__img-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 5;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
}

.swiper-button-next {
  right: 0;
}

.swiper-pagination {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 1rem;
  left: 0;
  width: 100%;
  z-index: 2;
}

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  margin: 3px;
  border-radius: 50%;
  background-color: #345dee;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: #8ba434;
}