// Навигация
.nav {
	margin: auto;
	font-family: $f-title;
	&__list {
		list-style: none;
		margin: 0;
		padding: 0;
		display: flex;
	}
	&__item {
		position: relative;
	}
	&__link {
		display: block;
		text-decoration: none;

		color: $c-txt;
	}
}

@include r(lg) {
	.nav > .nav__list > .nav__item > .nav__link {
		padding: 15px clamp(0.5em, 0.25vw, 2em);
	}
	.nav__item-sub > ul {
		position: absolute;
		min-width: 150px;
		left: 0;
		top: 100%;
		z-index: 100;
		background-color: #fff;
		filter: drop-shadow(0px 0px 5px rgba(#000, 0.3));
		flex-direction: column;
		visibility: hidden;
		opacity: 0;
		transform: translateY(100px);
		transition: all 0.3s 0.3s;
	}
