.burger {
	padding: 10px;
	display: inline-block;
	cursor: pointer;
	&__el {
		position: relative;
		display: block;
		height: 2px;
		width: 20px;
		background-color: #000;
		&:before,
		&:after {
			content: "";
			position: absolute;
			height: 2px;
			width: 20px;
			background-color: #000;
			transition: all 0.3s;
			transform-origin: 50% 50%;
		}
		&:before {
			transform: translateY(-4px);
		}
		&:after {
			transform: translateY(4px);
		}
	}
	&.is-open {
		.burger__el {
			background-color: transparent;
			&:before {
				transform: rotate(-45deg);
			}
			&:after {
				transform: rotate(45deg);
			}
		}
	}
}

.hc-nav-trigger {
	position: relative;
	top: 0;
	margin-left: $lg;
}

.hc-offcanvas-nav .nav__list {
	flex-direction: column;
}

.inner-nav {
	list-style: none;
	display: flex;
	padding: 0;
	margin: 30px 0 0;
	&--right {
		@include r("md") {
			margin-top: 0;
			grid-column-start: 2;
		}
	}
	&__item {
		&:not(:last-child) {
			margin-right: 34px;
		}
	}
	&__link {
		text-decoration: none;
		color: $c-txt;
		font-weight: 500;
		transition: color 0.2s;
	}
}

.nav-catalog {
	margin: 2 * $lg 0 0;
	padding: 0;
	display: flex;
	list-style: none;
	border-bottom: 1px solid $c-light;
	&__item {
		&:not(:first-child) {
			margin-left: 50px;
		}
		&.active .nav-catalog__link {
			border-color: $c-main;
		}
	}
	&__link {
		display: inline-block;
		text-decoration: none;
		font-weight: 500;
		color: $c-txt;
		padding-bottom: 1em;
		border-bottom: 4px solid transparent;
	}
}
