// ++++++++++++++++++++++++++++
// Form

input:not([type="submit"]),
input:not([type="checkbox"]),
input:not([type="radio"]),
textarea {
	padding: 1em 2em;
	border-radius: 100px;
	border: none;
	display: block;
}

input:focus,
textarea:focus {
	outline: none;
	box-shadow: 0 0 5px rgba($c-main, 0.5);
	@include placeholder() {
		opacity: 0.5;
	}
}

.form {
	max-width: 650px;
	margin: 0 auto;
}

.form-line {
	position: relative;
	margin-bottom: 0.75 * $lg;
	input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]) {
		width: 100%;
	}
	&--inline {
		display: flex;
		gap: $lg;
	}
}

.form__title {
	font-family: $f-title;
	font-size: 40px;
	line-height: 1.2;
	margin: 0 0 0.5em;
}

.form__txt {
	font-size: 18px;
	margin: 0 0 2.5em;
}

.form__footer {
	margin: 0;
}

.popup-form {
	display: none;
	max-width: 500px;
}

// ++++++++++++++++++++++++++++
// button

.btn {
	display: inline-block;
	flex: 0 0 auto;
	font-weight: 500;
	padding: 1em 1em 1em;
	text-decoration: none;
	line-height: 1;
	border-radius: 0.5em;
	border: none;
	appearance: none;
	color: $c-txt;
	position: relative;
	overflow: hidden;
	background: #e1e5f1;
	border-radius: 50px;
	text-align: center;
	&:not(.btn--title):hover {
		cursor: pointer;
		box-shadow: 0 0 0.8em rgba($c-txt, 0.3);
	}
	&--title {
		font-weight: 400;
		color: $c-main;
		padding: 0.5em 1.5em;
		margin-bottom: 50px;
	}

	&--big {
		padding: 1.5em 3em;
	}
	&--dark {
		background-color: $c-main;
		color: #fff;
		&:hover {
			color: #fff;
		}
	}
}

// Форма заказа звонка
.form-call-cnt {
	text-align: center;
	background: $c-light url("@/img/bg/bg_form_call.png") no-repeat bottom
		center;
}
